<template>
	<div class="newSite_box">
		<header>
			<Headnav head_left="" head_title='新增地址' head_right=" ">
				<router-link to="/shopSite" slot="left" tag="span">
					<van-icon name="arrow-left" />
				</router-link>
			</Headnav>
		</header>
		<div class="newSite_cont">
			<div class="newSite_input">
				<input :style="Color" @blur="showColor" @keydown="showInput($event)" v-model="userName" type="text" placeholder=" 请填写你的姓名">
				<span v-show="flag">请填写您的姓名</span>
				<router-link to="/searchlogin">
					<input v-model="setName" type="text" placeholder=" 小区/写字楼/学校等">
				</router-link>
				<input :style="Color1" @blur="showColor" @keydown="showInput1($event)" v-model="input2" type="text" placeholder=" 请填写详细送餐地址">
				<span v-show="flag2">请详细填写送餐地址</span>
				<input :style="Color2" @blur="showColor" @keydown="showInput2($event)" v-model="input3" type="text" placeholder=" 请填写能够联系到您的手机号">
				<span v-show="flag3">请输入正确的手机号</span>
				<input :style="Color3" @blur="showColor" @keydown="showInput3($event)" v-model="input4" type="text" placeholder=" 备用联系电话 (选填)">
				<span v-show="flag4">请输入正确的手机号</span>
			</div>
			<van-button @click="getSite()" v-if="userName&&setName&&input2&&input3&&input4"  type="primary" size="large">新增地址</van-button>
			<van-button v-else  disabled type="primary" size="large">新增地址</van-button>
		</div>
	</div>
</template>

<script>
	import Headnav from '.././About/headNav'
	import {mapMutations,mapState} from 'vuex'
	export default {
		name: "siteSearch",
		computed:{
			...mapState(['setName'])
		},
		data(){
			return{
				flag:false,
				flag2:false,
				flag3:false,
				flag4:false,
				userName:'',
				input2:'',
				input3:'',
				input4:'',
				Color:{},
				Color1:{},
				Color2:{},
				Color3:{},
			}
		},
		methods:{
			//提交新增地址到site页面中渲染
			getSite(){
				this.$store.commit('getst',{name:this.userName, site:this.input3,set:this.setName})
				this.$router.push({ path:'/shopSite'})
		},
		...mapMutations(['getlocalStorage']),
		showInput(e){
				this.userName=e.target.value
				if(e.keyCode==8&&this.userName==''){
					this.flag=true
					this.Color={border:'1px solid red'}
				}else{
					this.flag=false
				}
			},
			showInput1(e){
				this.input2=e.target.value
				if(e.keyCode==8&&this.input2==''){
					this.flag2=true
					this.Color1={border:'1px solid red'}
				}else{
					this.flag2=false
				}
			},
			showInput2(e){
				let phone =/^1[3456789]\d{9}$/
				this.input3=e.target.value
				if(e.keyCode==8&&this.input3==''){
					this.flag3=true
					this.Color2={border:'1px solid red'}
				}else{
					this.flag3=false
				}
				if(phone.test(!this.input3)){
					this.flag3=true
					this.Color2={border:'1px solid red'}
				}else{
					this.flag3=false
				}
			},
			showInput3(e){
				this.input4=e.target.value
				if(e.keyCode==8&&this.input4==''){
					this.flag4=true
					this.Color3={border:'1px solid red'}
				}else{
					this.flag4=false
				}
			},
			showColor(){
				this.Color={}
				this.Color1={}
				this.Color2={}
				this.Color3={}
			},
			//路由跳转
			// 获取地址
		},
		mounted(){
			this.getlocalStorage()
		},
		components:{
			Headnav
		}
	}
</script>

<style scoped>

